import { createRouter, createWebHistory } from 'vue-router'
import Home from '@/components/HomePage.vue'
import Subsite1 from '@/components/SubSite1.vue'
import Subsite2 from '@/components/SubSite2.vue'
import Subsite3 from '@/components/SubSite3.vue'

const routes = [
    { path: '/', name: 'Home', component: Home },
    { path: '/subsite1', name: 'Subsite1', component: Subsite1 },
    { path: '/subsite2', name: 'Subsite2', component: Subsite2 },
    { path: '/subsite3', name: 'Subsite3', component: Subsite3 },
]

const router = createRouter({
    history: createWebHistory(),
    routes,
})

export default router
